  .tabl1 {
   display: grid;
   grid-template-columns: 3fr 1fr 1fr 1fr 1fr 1fr 1fr 3fr;
   gap: 1px;
   background-color: black;
   border: 1px solid black;
   margin-bottom: 3px;
