.pdf {
    width: 100%;


    .contauner {
        width: 100%;

        .head {
            display: flex;
            justify-content: flex-end;

            .logo {
                width: 100px;

                img {
                    width: 100%;
                }
            }

        }



        .etage1 {
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            width: 100%;

            .etage1Left {
                div {
                    font-size: 15px;
                    text-align-last: left;
                }

                padding-bottom: 20px;
            }

            .etage1Right {
                display: flex;
                flex-direction: column;
                align-items: stretch;
                width: 250px;
                border-bottom: 1px solid gray;

                div {
                    font-size: 15px;
                }

                >div:nth-child(1),
                >div:nth-child(2),
                >div:nth-child(5) {
                    font-weight: 600;
                }

                >div:nth-child(1),
                >div:nth-child(2),
                >div:nth-child(3),
                >div:nth-child(4) {
                    text-align: right;
                    padding-right: 2px;
                }

                >div:nth-child(5) {
                    text-align: left;
                }
                >div:nth-child(1),
                >div:nth-child(5) {
                    background-color: rgba(128, 128, 128, 0.546);
                    border-top: 1px solid gray;
                    border-bottom: 1px solid gray;
                    padding-left: 2px;
                    padding-right: 4px;
                    
                }
                >div:nth-child(6),
                >div:nth-child(7),
                >div:nth-child(8),
                >div:nth-child(9) {
                    display: flex;
                    justify-content: space-between;
                    padding-left: 2px;
                    padding-right: 2px;
                }

            }

        }





        .etage2 {
            padding-top: 100px;
            >div:nth-child(1) {
                font-size: 25px;
                text-align: left;
                span {
                    font-weight: 700;
                    padding-right: 5px;
                }
            }
            >div:nth-child(2) {
                font-size: 23px;
                text-align: left;
                span {
                    font-weight: 700;
                    padding-right: 5px;
                }
            }
        }

        .etage3 {
            display: grid;
            grid-template-columns: 1fr 130px 130px;
            border-bottom: 1px solid gray;
            margin-top: 60px;
            div {
                border-top: 1px solid gray;
                text-align: center;
            }

            >div:nth-child(4),
            >div:nth-child(7),
            >div:nth-child(10),
            >div:nth-child(13),
            >div:nth-child(16) {
                text-align-last: left;
                padding-left: 3px;
            }
            >div:nth-child(2),
            >div:nth-child(3) {
                font-weight: 700;
            }
            >div:nth-child(7),
            >div:nth-child(13),
            >div:nth-child(16) {
                font-weight: 700;
            }

            >div:nth-child(16),
            >div:nth-child(17),
            >div:nth-child(18) {
                background-color: rgba(128, 128, 128, 0.546);;
            }
            >div:nth-child(10),
            >div:nth-child(11),
            >div:nth-child(12) {
                border-top: 0;
            }
        }

        .footer {
            font-size: 12px;
            margin-top: 150px;
            display: flex;
            flex-direction: column;
            gap: 5px;
            width: 100%;
            div {
                text-align-last: left;
            }
            >div:nth-child(3) {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: flex-end;
                >div:nth-child(1) {
                    span {
                        font-weight: 700;
                    }
                } 
                >div:nth-child(2) {
                    font-weight: 700;
                    font-size: 15px;
                }
            }
        }
    }


}