.container {
    width: 700px;

    * {
        font-size: 10px;
    }





    .header {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        .headerLogo {
            width: 250px;

            img {
                width: 100%;
            }
        }


        .headerInfo {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;

            h3 {
                padding: 0;
                margin: 0;
            }

            .headerPhones {
                margin-top: 10px;
                display: flex;
                gap: 15px;

                div {
                    text-align: left;
                }

            }

            .headerBot {
                margin-top: 10px;
                width: 100%;
                display: flex;
                justify-content: space-between;
            }

        }

    }

    .midle {
        margin-top: 30px;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        .midleDiv {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 15px;
            text-align: left;

        }

        .midleFlex {
            margin-bottom: 15px;
            display: flex;
            justify-content: space-between;
            width: 100%;

            div {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
            }

            div:last-child {
                padding-right: 20px;
            }
        }

    }

    .bottom {
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        div:first-child {
            width: 300px;
            img {
                width: 100%;
            }
        }
        div:last-child {
            font-size: 12px;
            padding-bottom: 10px;
            padding-left: 10px;
        }
        
    }


}