.pdf {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.container {
    width: 650px;
    margin: 0 auto;
    

    .header {
        display: flex;
        gap: 50px;



        .headerIcon {
            margin-top: 10px;
            width: 60px;

            img {
                width: 100%;
            }

        }

        .headerDisckription {
            display: flex;
            flex-direction: column;
            line-height: 1;
            font-weight: 500;
            font-size: 15px;
            align-items: flex-start;

            h2 {
                margin: 0;
                line-height: 1.4;
            }

            div:nth-child(2) {
                font-weight: normal;
                font-size: 12px;

                span {
                    font-weight: 700;
                }
            }

        }
    }

    .middle {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 12px;


        .middleLeft {
            display: flex;
            flex-direction: column;
            align-items: flex-start;

            h2 {
                font-size: 13px;
                color: #f24f00;
                margin: 0;
            }

            p {
                margin: 0;

                line-height: 1.2;
            }

            .middleLeftContacts {
                display: flex;
                gap: 10px;

                div {
                    display: flex;
                    gap: 2px;
                    padding-top: 3px;
                }

                .middleLeftContactsIcon {
                    width: 15px;

                    img {
                        width: 100%;
                    }

                }
            }

            .middleLeftSmall {
                font-size: 8px;

            }

            .middleLeftBottomString {
                color: #f24f00;
                font-weight: 600;

                span {
                    color: black;
                }

            }
        }

        .middleMid {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;

            p {
                margin: 0;
                line-height: 1.1;
            }

        }

        .middleRight {
            width: 40px;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            height: 150px;
            overflow: hidden;

            .middleRightStrix {
                transform: rotate(-90deg) translateY(30%);

                .barcode {
                    width: 150px;
                }




            }
        }
    }

    .additionally {
        * {
            font-size: 12px;
        }

        padding-top: 30px;
        width: 100%;



        .additionallyItems {
            border-bottom: 1px dashed rgba(0, 0, 0, 0.283);
            padding-bottom: 10px;
            display: flex;
            gap: 20px;


            .additionallyItem {
                display: flex;
                gap: 4px;


                .additionallyItemIcon {
                    width: 15px;

                    img {
                        width: 100%;
                    }
                }

                .additionallyItemText {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    gap: 3px;

                    div:nth-child(2) {
                        font-weight: 600;
                    }

                }
            }
        }


        .additionallyDescription {
            text-align-last: left;
            font-size: 12px;
            padding-top: 10px;
            padding-bottom: 30px;
        }
    }

    .result {
        width: 100%;
        background-color: #e6e6e6;
        position: relative;
        overflow: hidden;
        height: 100px;
        z-index: 1;
        display: flex;
        gap: 200px;
        align-items: center;

        .resultBack {
            position: absolute;
            top: 50%;
            left: 0;
            width: 400px;
            height: 400px;
            background-color: #f24f00;
            transform: rotate(45deg) translateX(-70%);
            z-index: 2;
        }

        .resultLeft {
            color: white;
            position: relative;
            z-index: 3;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: space-around;
            height: 100%;
            padding-left: 10px;
            div:nth-child(2) {
                font-size: 20px;
            }

        }

        .resultRight {
            position: relative;
            z-index: 3;
            font-size: 12px;
            align-items: flex-start;
            display: flex;
            flex-direction: column;
            div {
                span {
                    font-weight: 600;
                }
            }
        }
    }

    .podResult {
        margin-top: 10px;
        display: grid;
        grid-template-columns: 3.5fr 1fr 1fr 1fr 1fr;
        div {
            text-align-last: left;
            font-size: 12px;
            padding: 1px 2px;
        }

        div:nth-child(1) {
            font-weight: 600;
        }
        div:nth-child(1),
        div:nth-child(2),
        div:nth-child(3),
        div:nth-child(4),
        div:nth-child(5) {
            background-color: #f24f00;
            color: #ffffff;
        }

        div:nth-child(6),
        div:nth-child(7),
        div:nth-child(8),
        div:nth-child(9),
        div:nth-child(10) {
            border-bottom: 1px solid black;
        }

        div:nth-child(11),
        div:nth-child(12),
        div:nth-child(13),
        div:nth-child(14),
        div:nth-child(15) {
            border-bottom: 1px dashed rgba(0, 0, 0, 0.279);
        }
    }

   
}


.footer {
    


    .containerBottom  {
        padding-top: 6px;
        border-top: 1px dashed rgba(0, 0, 0, 0.279);
        .footerContent {
            display: flex;
            justify-content: space-between;
            .footerContentLeft {
                display: flex;
                align-items: flex-start;
                flex-direction: column;
                gap: 10px;
                div {
                    text-align: left;
                    font-size: 8px;
                    span {
                        font-weight: 600;
                    }
                }

                div:nth-child(2) {
                    color: #f24f00;
                }


            }
    
            .footerContentRight {
                display: flex;
                font-size: 8px;
                
                flex-direction: column;
                align-items: flex-start;
                div {
                    text-align: left;
                }
            }
        }
    
        .footerStrana {
            font-size: 12px;
            text-align: right;
        }
    }
    

    
}



