.container {
    width: 700px;

    * {
        font-size: 10px;
    }





    .header {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        .headerLogo {
            width: 250px;

            img {
                width: 100%;
            }
        }


        .headerInfo {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;

            h3 {
                padding: 0;
                margin: 0;
            }

            .headerPhones {
                margin-top: 10px;
                display: flex;
                gap: 15px;

                div {
                    text-align: left;
                }

            }

            .headerBot {
                margin-top: 10px;
                width: 100%;
                display: flex;
                justify-content: space-between;
            }

        }

    }





    .content {
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 20px;


        .contentTop {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            gap: 10px;

            * {
                font-size: 12px;
            }

            h2 {
                margin: 0;

            }

            div {
                display: flex;
                flex-direction: column;
                align-items: flex-start;

                p {
                    margin: 0;
                }
            }
        }

        .contentMid {
            width: 100%;

            display: flex;
            justify-content: space-between;

            div {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                p {
                    margin: 0;
                }
            }
            div:last-child {
                padding-right: 50px;
            }

        }

        .contentTabl {
            display: grid;
            grid-template-columns: 1fr 3fr 1.3fr 1.5fr 1fr 1fr 2fr;
            border: 3px solid rgb(156, 154, 154);
            gap: 3px;
            background-color: rgb(156, 154, 154);
            div {
                text-align: center;
                background-color: white;
                display: flex;
                align-items: center;    
                flex-direction: column;
                justify-content: center;
                p {
                    margin: 0;
                }
                p:nth-child(2),
                p:nth-child(3) {
                    font-size: 8px;
                }

            }

            div:nth-child(1),
            div:nth-child(2),
            div:nth-child(3),
            div:nth-child(4),
            div:nth-child(5),
            div:nth-child(6),
            div:nth-child(7){
                font-weight: 700;
            }
        }

        .contentBottom {
            text-align-last: left;
            font-size: 11px;
        }
    }



    .footer {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        div:nth-child(2) {
            width: 350px;
            img {
                width: 100%;
            }
        }
    }
}