.container {
 
}

.Header {
    display: flex;
    gap: 27px;
    border-bottom: 2px solid gray;
  }
  
.logo {
    width: 230px;
    img {
        width: 100%;
    }
}

.headerDescription {
    color: rgba(0, 0, 0, 0.531);
    font-size: 9px;
    padding-top: 14px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.mid {
    padding-top: 15px;
    font-size: 10px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 22px;
    >div {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

}

.midTutle {
   font-size: 11px;
   font-weight: 500;
}

.midNormal {

    font-weight: 500;
    
    span {
        font-weight: 400;
        display: inline;
    }
}

.tabl {
    margin-top: 20px;
    display: grid;
    grid-template-columns: 2.5fr 4fr 1fr 2fr 2fr 2fr 2fr 2fr 2fr 2fr;
    font-size: 9px;
    background-color: black;
    gap: 1px;
    border: 1px solid black; 
    div {
        background-color: white;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        padding: 2px;
    }
    div:nth-child(1),
    div:nth-child(2),
    div:nth-child(3),
    div:nth-child(4),
    div:nth-child(5),
    div:nth-child(6),
    div:nth-child(7),
    div:nth-child(8),
    div:nth-child(9),
    div:nth-child(10)
     {
        font-weight: 500;
    }

}

.bot {
    display: flex;
    font-size: 10px;
    align-items: flex-end;
    margin-top: 10px;
    justify-content: space-around;
    div{
        img {
            width: 300px;
        }
    }
}


.tablMaket2 {
    margin-top: 15px;
    display: grid;
    grid-template-columns: 1.3fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 1px;
    background-color: black;
    padding: 1px;
    div {
        background-color: white;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        padding: 15px;
        font-size: 14px;
    }
    div:nth-child(1),
    div:nth-child(2),
    div:nth-child(3),
    div:nth-child(4) {
        font-weight: 500;
    }
}